@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Nunito', sans-serif;background:#fce77d;color:#f96167}::selection{color:#fce77d;background:#f96167}#bigNotesContainer{display:flex;align-items:center;justify-content:center;flex-direction:column;max-width:400px;margin:50px auto 0 auto}#bigNotesContainer form{width:100%}#bigNotesContainer form header{font-weight:700;text-align:right}#bigNotesContainer form header #react-color{color:#61dafb}#bigNotesContainer form header #redux-color{color:#764abc}#bigNotesContainer form header #react-color,#bigNotesContainer form header #redux-color,#bigNotesContainer form header #dash{margin:0;display:inline-block}#bigNotesContainer form header #mark-header{font-size:40px;margin:0}#bigNotesContainer form p{font-size:24px;margin:0 0 2px 0;text-align:right;font-weight:600}#bigNotesContainer form button{display:block;margin:5px 0 0 auto;border:3px solid #f96167;font-size:17px;font-weight:800;cursor:pointer;-webkit-transition:0.5s;transition:0.5s}#bigNotesContainer form button:hover{color:#fce77d;background:#f96167}#bigNotesContainer form textarea{outline:none;box-sizing:border-box;width:100%;min-height:100px;border:3px solid #f96167;padding:5px;resize:vertical}#bigNotesContainer #notesContainer{display:flex;width:100%;flex-direction:column}#bigNotesContainer #notesContainer .todoItem{box-sizing:border-box;margin:20px 0;width:100%;border:3px solid #f96167;padding-bottom:5px}#bigNotesContainer #notesContainer .todoItem .noteTextCont .data{text-align:left;font-weight:700;font-size:18px;margin:0;color:#fce77d;background:#f96167;padding:3px}#bigNotesContainer #notesContainer .todoItem .noteTextCont .data #trash{display:inline-block;text-align:right;margin:3px 5px 0 15px;font-size:17px;font-weight:800;cursor:pointer;color:#fce77d;background:#f96167;float:right}#bigNotesContainer #notesContainer .todoItem .noteTextCont .data #trash path{color:#fce77d}#bigNotesContainer #notesContainer .todoItem .noteTextCont .noteBefore{display:block;border:2.5px solid #f96167;padding:5px;box-sizing:border-box;text-align:right;font-size:20px;font-weight:600;word-wrap:break-word;margin:20px 5px}#bigNotesContainer #notesContainer .todoItem .noteTextCont .noteTextArea{outline:none;box-sizing:border-box;min-height:100px;border:2.5px solid #f96167;padding:5px;resize:vertical;font-size:18px;margin:5px 5px 0 5px;width:97.5%}@media screen and (max-width: 415px){#bigNotesContainer{margin:10px;max-width:100%}}@media screen and (max-width: 325px){#bigNotesContainer #notesContainer .todoItem .noteTextCont .data{min-height:50px}}

